import React from "react";
import { NavLink } from "react-router-dom";
import "../style/Navigation.scss";
import { library } from "@fortawesome/fontawesome-svg-core";
import {
  faEnvelope,
  faKey,
  faHome,
  faNewspaper,
  faCode,
  faGamepad,
  faAddressCard,
  faRss,
  faCodeBranch
} from "@fortawesome/free-solid-svg-icons";
import { faGithub, faGithubSquare } from "@fortawesome/free-brands-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

library.add(
  faEnvelope,
  faKey,
  faHome,
  faNewspaper,
  faCode,
  faGamepad,
  faAddressCard,
  faRss,
  faCodeBranch,
  faGithubSquare,
  faGithub
);

const list = [
  {
    name: <FontAwesomeIcon icon="address-card" size="4x" />,
    path: "/",
    exact: true
  },
  {
    name: <FontAwesomeIcon icon={["fab", "github"]} size="4x" />,
    path: "/repos"
  },
  // {
  //   name: <FontAwesomeIcon icon="rss" size="4x" />,
  //   path: "/news"
  // },
  {
    name: <FontAwesomeIcon icon="gamepad" size="4x" />,
    path: "/game"
  }
];

const Navigation = () => {
  const menu = list.map(item => (
    <li key={item.path}>
      <NavLink to={item.path} exact={item.exact ? item.exact : false}>
        {item.name}
      </NavLink>
    </li>
  ));
  return (
    <nav>
      <ul>{menu}</ul>
    </nav>
  );
};

export default Navigation;
