import React, { Component } from "react";
import { Link } from "react-router-dom";
import "../style/Repos.scss";

import { library } from "@fortawesome/fontawesome-svg-core";
import { faKey, faCode, faPlayCircle } from "@fortawesome/free-solid-svg-icons";
import { faGithub, faGithubSquare } from "@fortawesome/free-brands-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

library.add(faKey, faPlayCircle, faCode, faGithub, faGithubSquare);

class Repos extends Component {
  state = {
    repositories: [],
    active: false
  };

  componentDidMount() {
    fetch("https://api.github.com/users/gucek82/repos")
      .then(response => response.json())
      .then(resposneData => {
        this.setState({
          repositories: resposneData
        });
      });
  }

  sorderdObj = (a, b) => {
    if (a.name > b.name) return -1;
    if (a.name < b.name) return 1;
    return 0;
  };

  render() {
    let repos = this.state.repositories.sort(this.sorderdObj);
    repos = this.state.repositories.map(repo => (
      <div className="single-repo" key={repo.id}>
        <span className="github-logo">
          <FontAwesomeIcon icon={["fab", "github"]} size="4x" color="#fff" />
        </span>
        <h4>{repo.name}</h4>
        <h6>{repo.language}</h6>
        <p>{repo.description}</p>

        <p>
          <span>
            {" "}
            {repo.has_pages ? (
              <a href={"https://gucek82.github.io/" + repo.name}>
                <FontAwesomeIcon icon="play-circle" color="#fff" size="2x" />{" "}
                DEMO
              </a>
            ) : (
              ""
            )}
          </span>
          <span>
            <FontAwesomeIcon icon="code" color="#fff" size="2x" />{" "}
            <a href={repo.html_url}>Github</a>
          </span>
        </p>
      </div>
    ));

    return (
      <div className="reposContainer">
        <h1>Repozytorium GitHub</h1>
        <div className="reposList">
          {this.state.active ? repos : repos.slice(0, 6)}
        </div>
        <button
          onClick={() => {
            this.setState({
              active: !this.state.active
            });
          }}
        >
          {this.state.active ? "Mniej" : "Więcej"}
        </button>
      </div>
    );
  }
}

export default Repos;
