import React, { Component } from "react";
import "../style/Game.scss";
import papier from "../img/hand.png";
import nozyczki from "../img/fingers.png";
import kamien from "../img/punch.png";

class Game extends Component {
  hand = [papier, nozyczki, kamien];
  state = {
    numbers: 0,
    wins: 0,
    losses: 0,
    draws: 0,
    playerHand: "",
    aiHand: "",
    result: "",
    style: ""
  };

  startGame = () => {
    if (this.state.aiHand || !this.state.playerHand)
      return alert("Wybierz rękę");

    const aiChoiceIndex = Math.floor(Math.random() * this.hand.length);
    this.setState({
      aiHand: this.hand[aiChoiceIndex],
      numbers: this.state.numbers + 1
    });
  };

  playerChoice = e => {
    this.setState({
      playerHand: e.target.src,
      result: "",
      aiHand: ""
    });
  };

  componentDidUpdate() {
    const { playerHand, aiHand } = this.state;
    if (
      playerHand !== "" &&
      aiHand !== "" &&
      this.state.result === "" &&
      ((playerHand.slice(25) === papier && aiHand === kamien) ||
        (playerHand.slice(25) === kamien && aiHand === nozyczki) ||
        (playerHand.slice(25) === nozyczki && aiHand === papier))
    ) {
      this.setState({
        result: "Wygrałeś",
        wins: this.state.wins + 1,
        style: "green"
      });
    } else if (
      playerHand !== "" &&
      aiHand !== "" &&
      this.state.result === "" &&
      ((playerHand.slice(25) === kamien && aiHand === papier) ||
        (playerHand.slice(25) === nozyczki && aiHand === kamien) ||
        (playerHand.slice(25) === papier && aiHand === nozyczki))
    ) {
      this.setState({
        result: "Przegrałeś",
        losses: this.state.losses + 1,
        style: "red"
      });
    } else if (
      playerHand !== "" &&
      aiHand !== "" &&
      this.state.result === "" &&
      (playerHand.slice(25) === aiHand || playerHand === aiHand)
    ) {
      this.setState({
        result: "Remis",
        draws: this.state.draws + 1,
        style: "grey"
      });
    }
  }

  render() {
    return (
      <div>
        <h1>GRA "PAPIER, NOŻYCE, KAMIEŃ"</h1>

        <div className="select">
          <h3>Proszę wybierz:</h3>

          <img
            data-option="papier"
            src={papier}
            alt=""
            title="Papier"
            onClick={this.playerChoice}
          />
          <img
            data-option="kamień"
            src={kamien}
            alt=""
            title="Kamień"
            onClick={this.playerChoice}
          />
          <img
            data-option="nozyczki"
            src={nozyczki}
            alt=""
            title="Nozycze"
            onClick={this.playerChoice}
          />
        </div>

        <button className="start" onClick={this.startGame}>
          Let's play!
        </button>

        <div className="panel-left">
          <h3>Wynik gry</h3>
          <p>
            Twój wybór:{" "}
            <span data-summary="your-choice">
              <img src={this.state.playerHand} alt="" />
            </span>
          </p>
          <p>
            Wybór komputera:{" "}
            <span data-summary="ai-choice">
              <img src={this.state.aiHand} alt="" />
            </span>
          </p>
          <h2>
            Rezultat gry:{" "}
            <span data-summary="who-win" className={this.state.style}>
              {this.state.result}
            </span>
          </h2>
        </div>

        <div className="panel-right">
          <h3>Aktualne wyniki</h3>
          <p className="numbers">
            liczba gier: <span>{this.state.numbers}</span>
          </p>
          <p className="wins">
            wygranych: <span>{this.state.wins}</span>
          </p>
          <p className="losses">
            przegranych: <span>{this.state.losses}</span>
          </p>
          <p className="draw">
            remisów: <span>{this.state.draws}</span>
          </p>
        </div>
      </div>
    );
  }
}

export default Game;
